.datashow {
  z-index: 2;
  width: 100%;
  padding: 0px 0px 0px 0px;
  background: #FFFFFF;
  box-shadow: 3px 2px 10px 0 rgba(165, 165, 165, 0.5);
  height: 100%;
}
.ant-table-pagination.ant-pagination {
  text-align: center;
  width: 100%;
  z-index: 999 !important;
}
.ant-table-pagination.ant-pagination li {
  margin-bottom: 5px;
}
.scrolls {
  height: 20px;
  overflow-y: auto;
}
.ant-table {
  font-size: 12px;
}
.scrolls ::-webkit-scrollbar {
  display: none;
}
.tableContent {
  height: 100%;
  margin-top: -22px;
}
.tableContent .ant-table-wrapper {
  height: 100%;
}
.tableContent .ant-spin-nested-loading {
  height: 100%;
}
.tableContent .ant-spin-container {
  height: 100%;
}
.tableContent .ant-spin-container .ant-table {
  height: calc(100% - 50px);
  overflow-y: auto;
}
::-webkit-scrollbar {
  width: 2px;
  height: 4px;
}
/*定义滚动条轨道（凹槽）样式*/
::-webkit-scrollbar-track {
  border-radius: 10px;
  border: #ffffff;
  background: #ffffff;
}
/*定义滑块 样式*/
::-webkit-scrollbar-thumb {
  border-radius: 14px;
  background-clip: padding-box;
  background-color: #C4C4C4;
}
.ant-table-tbody > tr > td,
.ant-table tfoot > tr > th,
.ant-table tfoot > tr > td {
  position: relative;
  padding: 0px 12px;
  overflow-wrap: break-word;
}
.ant-table-thead > tr > th {
  position: relative;
  padding: 10px 12px;
  overflow-wrap: break-word;
}
.ant-table-tbody tr.ant-table-row {
  height: 45px;
}
